export default{
	data(){
		return{
			icon: '../../static/Informatic.png',
		}
	},
	props:{
		lab:{
			required: true
		},
		displayArrow:{
			type: Boolean
		},
		color:""
	},
	computed: {
		showCode () {
			let lab = this.lab;
			if (lab.RoomDisplayState === '空闲') {
				return lab.RoomCode
			} else {
				return lab.RoomDisplayState
			}
		}
	},
	methods:{
		labDetail(id) {
			uni.navigateTo({ 
				url: './roomDetail?id=' + id
			})
		}
	}
}	
